/* Base Styles */
:root {
  --primary-color: #2c3e50;
  --secondary-color: #e74c3c;
  --light-color: #f8f9fa;
  --dark-color: #2c3e50;
  --text-color: #333;
  --text-light: #7f8c8d;
  --transition: all 0.3s ease;
}

body {
  font-family: 'Montserrat', sans-serif;
  color: var(--text-color);
  line-height: 1.6;
}

.container {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

/* Hero Section */
.hero-section {
  position: relative;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
  overflow: hidden;
}

.hero-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  /* makes the video cover entire hero area */
  z-index: -2;
}

.hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  /* dark overlay for readability */
  z-index: -1;
}

.hero-content {
  position: relative;
  z-index: 2;
  max-width: 800px;
  margin: 0 auto;
  animation: fadeIn 1s ease-out;
}

.hero-content h1 {
  font-family: 'Didot Bold', serif;
  font-size: 4rem;
  margin-bottom: 1rem;
  font-weight: 700;
  letter-spacing: 2px;
}

.hero-tagline {
  font-family: 'Avenir', sans-serif;
  font-size: 1.8rem;
  margin-bottom: 1.5rem;
  font-weight: 300;
  letter-spacing: 8px;
}

/* Buttons */
.cta-button,
.view-all-button,
.category-link {
  display: inline-block;
  padding: 15px 35px;
  border-radius: 10px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: var(--transition);
  text-decoration: none;
}

.view-all-container {
  text-align: center;
  margin: 2rem 0;
}

.view-all-button {
  background-color: var(--primary-color);
  color: white;
  border: 2px solid var(--primary-color);
  text-align: center;
}

.view-all-button:hover {
  background-color: transparent;
  color: var(--primary-color);
}

/* Section Headers */
.section-header {
  text-align: center;
  margin-bottom: 4rem;
}

.section-header h2 {
  font-size: 2.5rem;
  color: var(--dark-color);
  margin-bottom: 1rem;
  position: relative;
  display: inline-block;
}

.section-header h2::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background: var(--secondary-color);
}

.section-header p {
  font-size: 1.1rem;
  color: var(--text-light);
  max-width: 600px;
  margin: 0 auto;
}

.featured-products {
  margin-top: 40px;
}

/* new section */
/* --- GRID WRAPPER (unchanged, tweak height if you like) --- */
.perfume-section{
  display:grid;
  grid-template-columns:repeat(2,1fr);
  grid-template-rows:repeat(2, 460px);
  max-width:1200px;
  margin:0 auto;
  padding:40px 20px;
  font-family:Georgia, serif;
  color:#000;
}

/* Image cells */
.perfume-top-left,
.perfume-bottom-right{
  position:relative;
}

/* ------------- TOP-LEFT (big image on the RIGHT 80%) ------------- */
.perfume-top-left .perfume-large-img-top{
  position:absolute;
  right:0;                   /* stick to the right edge */
  top:0;
  height:100%;
  width:80%;                 /* big image takes 80% */
  object-fit:cover;
}

.perfume-top-left .perfume-small-img-top{
  position:absolute;
  top:50%;
  left:20%;                  /* place on the 20% boundary */
  transform:translate(-50%, -50%); /* straddle that boundary */
  width:clamp(110px, 24%, 180px);
  box-shadow:0 8px 20px rgba(0,0,0,.12);
  z-index:2;
}

/* ------------- BOTTOM-RIGHT (big image on the LEFT 80%) ------------- */
.perfume-bottom-right .perfume-large-img-bottom{
  position:absolute;
  left:0;                    /* stick to the left edge */
  top:0;
  height:100%;
  width:80%;
  object-fit:cover;
}

.perfume-bottom-right .perfume-small-img-bottom{
  position:absolute;
  top:50%;
  right:20%;                  /* place on the 80% boundary (right side) */
  transform:translate(50%, -50%);  /* half outside, half overlapping */
  width:clamp(120px, 26%, 200px);
  box-shadow:0 8px 20px rgba(0,0,0,.12);
  z-index:2;
}

/* Text Blocks */
.perfume-top-right,
.perfume-bottom-left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px 40px;
}

.perfume-top-right {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;   /* push content to the right */
  text-align: right;       /* align text to the right */
  padding: 20px 40px;
}


.perfume-small-text {
  font-size: 12px;
  font-weight: 600;
  letter-spacing: 2px;
  margin-bottom: 15px;
  text-transform: uppercase;
}

.perfume-title {
  font-size: 2.8rem;
  line-height: 1.2;
  margin-bottom: 15px;
}

.perfume-desc {
  font-size: 0.95rem;
  color: #555;
  margin-bottom: 20px;
  max-width: 320px;
}

.btn-black {
  background-color: #000;
  color: #fff;
  border: none;
  padding: 12px 22px;
  font-weight: 700;
  font-size: 0.9rem;
  cursor: pointer;
  text-transform: uppercase;
  max-width: 140px;
}

.btn-black:hover {
  background-color: #222;
}

/* --- RESPONSIVE STYLES --- */

/* For tablets and smaller screens */
@media (max-width: 992px) {
  .perfume-section {
    grid-template-rows: repeat(2, 380px);
  }

  .perfume-title {
    font-size: 2.2rem;
  }

  .perfume-top-right,
  .perfume-bottom-left {
    padding: 20px 30px;
  }
}

/* For mobile landscape and smaller tablets */
@media (max-width: 768px) {
  .perfume-section {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, auto);
    gap: 30px;
  }

  /* Reorder grid items for better flow */
  .perfume-top-left {
    grid-row: 1;
    height: 400px;
  }

  .perfume-top-right {
    grid-row: 2;
    align-items: center;
    text-align: center;
  }

  .perfume-bottom-left {
    grid-row: 3;
    align-items: center;
    text-align: center;
  }

  .perfume-bottom-right {
    grid-row: 4;
    height: 400px;
  }

  .perfume-title {
    font-size: 2rem;
  }

  .perfume-desc {
    max-width: 100%;
  }

  /* Adjust image positioning for mobile */
  .perfume-top-left .perfume-small-img-top,
  .perfume-bottom-right .perfume-small-img-bottom {
    width: 160px;
  }
}

/* For mobile portrait */
@media (max-width: 576px) {
  .perfume-section {
    padding: 20px 15px;
    gap: 20px;
  }

  .perfume-top-left,
  .perfume-bottom-right {
    height: 350px;
  }

  .perfume-top-right,
  .perfume-bottom-left {
    padding: 15px;
  }

  .perfume-title {
    font-size: 1.8rem;
  }

  .perfume-small-text {
    font-size: 11px;
  }

  .perfume-desc {
    font-size: 0.9rem;
  }

  /* Adjust image positioning for mobile */
  .perfume-top-left .perfume-small-img-top,
  .perfume-bottom-right .perfume-small-img-bottom {
    width: 140px;
  }
}

/* For very small screens */
@media (max-width: 400px) {
  .perfume-title {
    font-size: 1.6rem;
  }

  .btn-black {
    padding: 10px 18px;
    font-size: 0.8rem;
  }

  .perfume-top-left .perfume-small-img-top,
  .perfume-bottom-right .perfume-small-img-bottom {
    width: 120px;
  }
}


/* Products Grid */
.products-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 2rem;
  margin: 3rem 0;
}

.product-card {
  background: white;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  transition: var(--transition);
}

.product-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.15);
}

.product-image {
  height: 350px;
  overflow: hidden;
}

.product-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: var(--transition);
}

.product-card:hover .product-image img {
  transform: scale(1.05);
}

.product-info {
  padding: 1.5rem;
  text-align: center;
}

.product-info h3 {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
}

.price-section {
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--secondary-color);
  margin: 1rem 0;
}

/* Loading Spinner */
.loading-spinner {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.spinner {
  width: 50px;
  height: 50px;
  border: 5px solid rgba(0, 0, 0, 0.1);
  border-top: 5px solid var(--secondary-color);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

/* Animations */
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive Design */
@media (max-width: 1024px) {
  .hero-content h1 {
    font-size: 3rem;
  }

  .hero-tagline {
    font-size: 1.5rem;
  }
}

@media (max-width: 768px) {
  .hero-content h1 {
    font-size: 2.5rem;
  }

  .hero-tagline {
    font-size: 1.3rem;
  }

  .section-header h2 {
    font-size: 2rem;
  }

  .products-grid {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }
}

@media (max-width: 480px) {
  .hero-section {
    padding: 120px 20px;
  }

  .hero-content h1 {
    font-size: 2rem;
  }

  .hero-description {
    font-size: 1rem;
  }

  .categories-grid {
    grid-template-columns: 1fr;
  }

  .section-header {
    margin-bottom: 2rem;
  }
}